/***********************************
// Edit Account Info
**************************************/
.rs-select-editAccount {
  position: absolute !important;
  top: -3% !important;
}

input[type="text"] {
  font-size: rem(24px);
}

.ra-select {
  padding: 10px;
  /* Replacing Default Arrow */
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url('data:image/svg+xml;utf8,<svg width="24" height="25" viewBox="0 0 24 25" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M4 10.127L12 18.127L20 10.127H4Z" fill="%238E8E93"/></svg>');
  background-repeat: no-repeat;
  background-position-y: 50%;
  background-position-x: 95%;
  min-height: 56px;
  border-radius: 4px;
  background-color: $white;
  cursor: pointer;

  @media (max-width: 992px) {
    min-height: 40px;
  }
}

.form-width {
  @media (max-width: 768px) {
    width: 100% !important;
  }
}

.ra-select:focus {
  outline: none;
  border: 1px solid $borderColor;
}

.ra-select option {
  background-color: #fff;
}

